.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(rgb(125, 176, 36), rgb(175, 29, 163) );
  
}

.Board {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.status {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.Block {
  width: 150px;
  height: 150px;
  border: 5px solid black;
  background-size: cover;
  display: inline-block;
}

.Block:hover {
  opacity: 0.8;
  cursor: pointer;
}

.mark2 {
  background-image: url('images/dot.png');
}

.mark1 {
  background-image: url('images/cross.png');
}

.BlockRow {
  display: flex;
}

.restart-button {
  font-size: 18px;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: #738597;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.restart-button:hover {
  background-color: #0056b3;
  color: #fff;
}

.heading {
  color: #b62626;
  min-height: fit-content;
  text-align: center;
  font-size: 100px; /* Decreased font size */
  font-weight: bold; /* Removed bold styling */
  opacity: 3px; /* Reduced opacity to make it quieter */
  margin-top: 50px; /* Added margin-top */
  margin-bottom: -120px;
}

  
.PlayerNameInput {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.player-inputs {
  display: flex;
  gap: 1rem;
}

.player-input {
  display: flex;
  flex-direction: column;
}

input[type='text'] {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

button:hover {
  background-color: #0056b3;
}
